.cal-background {
  display:flex;
  justify-content: center;
  align-items: center;
  height:100vh;
  background: #c2c2d6;
}

body {
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: default;
}

.calculator {
  border: 2px solid black;
  padding: 5px; 
  background: black;
  width: 320px; 
  position: relative;
  user-select: none;
  cursor: default;
}

button {
  position: relative;
  height: 60px;
  width:75px;
  color: white;
  border: none;
  outline: 1px solid black;
  font-size: 20px;
  cursor:default;
  background: grey;
  margin:0.05em;
}

button:hover{
  color: black;
  outline: 0.05em solid white;
  z-index:3;

}

